import { mapGetters } from "vuex";
import { createHelpers } from "vuex-map-fields";
import { required, url, minLength, numeric } from "vuelidate/lib/validators";
import { Money } from "v-money";
import { QField } from "quasar";
const { mapFields } = createHelpers({
  getterType: "events/getField",
  mutationType: "events/updateField",
});

export default {
  name: "EventProfile",
  components: {
    Money,
    QField,
  },
  data() {
    return {
      waiting: false,
      files: null,
      file: null,
      money: {
        decimal: ",",
        thousands: ".",
        prefix: "R$ ",
        precision: 2,
        maxlength: 14,
        masked: false,
      },
    };
  },
  validations: {
    categoryId: {
      required,
    },
    name: {
      required,
    },
    date: {
      required,
    },
    time: {
      required,
    },
    local: {
      required,
    },
    street: {
    },
    neighborhood: {
    },
    number: {
      numeric
    },
    zipcode: {
      minLength: minLength(8),
    },
    description: {
      required,
    },
    link: {
      url,
    },
    files: {},
  },
  computed: {
    ...mapFields({
      category: "categorySelected",
      categoryId: "eventForm.categoryId",
      name: "eventForm.title",
      date: "eventForm.date",
      time: "eventForm.time",
      ticket: "eventForm.ticket",
      local: "eventForm.local",
      street: "eventForm.street",
      neighborhood: "eventForm.neighborhood",
      number: "eventForm.number",
      city: "eventForm.city",
      zipcode: "eventForm.zipcode",
      description: "eventForm.description",
      link: "eventForm.link",
      images: "eventForm.imageIds",
    }),
    ...mapGetters({
      categories: "categories/loadCategories",
      user: "users/getCurrentUser",
    }),
    categoryErrorMessage() {
      if (!this.$v.categoryId.required) {
        return "Esse campo é obrigatório";
      }
      return "";
    },
    nameErrorMessage() {
      if (!this.$v.name.required) {
        return "Esse campo é obrigatório";
      }
      return "";
    },
    dateErrorMessage() {
      if (!this.$v.date.required) {
        return "Esse campo é obrigatório";
      }
      return "";
    },
    timeErrorMessage() {
      if (!this.$v.time.required) {
        return "Esse campo é obrigatório";
      }
      return "";
    },
    localErrorMessage() {
      if (!this.$v.local.required) {
        return "Esse campo é obrigatório";
      }
      return "";
    },
    streetErrorMessage() {
      return "";
    },
    neighborhoodErrorMessage() {
      return "";
    },
    numberErrorMessage() {
      return "";
    },
    zipcodeErrorMessage() {
      if (!this.$v.zipcode.minLength) {
        return "Entre com um CEP válido";
      }
      return "";
    },
    descriptionErrorMessage() {
      if (!this.$v.description.required) {
        return "Esse campo é obrigatório";
      }
      return "";
    },
    linkErrorMessage() {
      if (!this.$v.link.url) {
        return "Entre com uma url válida";
      }
      return "";
    },
    filesErrorMessage() {
      return "";
    },
  },
  methods: {
    updateFiles(files) {
      if (Array.isArray(files) === false || files.length === 0) {
        this.files = null;
      } else if (Array.isArray(this.files) === true) {
        const diff = this.files.filter((file) => files.indexOf(file) === -1);
        if (diff.length === 1 && this.files.length > 1) {
          this.files = files.slice();
        } else if (this.files.length < 3) {
          this.files = diff.concat(files);
        }
      } else {
        this.files = files.slice();
      }
    },
    sendForm(){
      this.$store.dispatch("events/createNewEvent")
        .then(() => {
          this.date = "";
          this.time = "";
          this.ticket = "";
          this.site = "";
          this.street = "";
          this.neighborhood = "";
          this.number = "";
          this.city = "";
          this.zipcode = "";
          this.description = "";
          this.link = "";
          this.imgUrl = "";
          this.category = "";
          this.categoryId = null;
          this.name = "";
          this.local = "";
          this.images = [];
        });
      this.$router.push({ name: "Agenda" });
      this.waiting = false;
    },
    confirmCreate() {
      this.$v.$touch();
      if (!this.$v.$anyError) {
        if (this.waiting){
          this.$q.notify({
            message: "Por favor, aguarde.",
            position: 'top-right',
          });
          return;
        };
        this.waiting = true;

        //se tiver imagens
        if(this.files) {
          this.$store
            .dispatch("images/uploadArray", { files: this.files })
            .then((fileIds) => {
            this.images = fileIds;
            this.sendForm();
          })
          .catch((error) => {
            this.waiting = false;
          });
        }
        //se não tiver imagens
        else {
          this.sendForm();
        }
      } else {
        this.$q.notify({
          message: "Por favor, preencha os campos corretamente.",
          position: 'top-right',
        });
      }
    },
  },
};
